.popup__container {
  width: 430px;
  background: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 34px 36px;
  position: relative;
  box-sizing: border-box;
}

@media screen and (max-width: 565px) {
  .popup__container {
    margin: 0 10px;
  }
}
